import { CrossSellingPointer } from "../../others";
import { RPM } from ".";
import $ from "jquery";

const crossRpmMove = () =>
    CrossSellingPointer.isActionAvailable(RPM, "move") &&
    (window as any).rml?.hooks.register("attachment/move/finished", (ids: number[], to: number[]) => {
        new CrossSellingPointer($(`.rml-container:visible li[data-li-id="${to}"]`), RPM, "move");
    });

export { crossRpmMove };
